.cp-chart{margin:0;height:100%;position:relative}.cp-chart svg{width:100%;height:100%}.cp-chart .axis path,.cp-chart .axis line{fill:none;stroke:rgba(51,64,77,.1);shape-rendering:crispEdges}.cp-chart .ticks-axis path,.cp-chart .ticks-axis line{fill:none;stroke:none;shape-rendering:crispEdges}.cp-chart .ticks-axis g.tick text{fill:#000;font-size:11px}.cp-chart .axis text{fill:RGBA(0, 0, 0, 0.6)}.cp-chart .grid-line .tick{stroke:RGBA(0, 0, 0, 0.08);stroke-opacity:.7;shape-rendering:crispEdges}.cp-chart .grid-line .tick line{stroke:RGBA(0, 0, 0, 0.08);stroke-opacity:.7;shape-rendering:crispEdges}.cp-chart .grid-line path{stroke-width:0}.tooltip{border-radius:3px;padding:7px 10px;background:rgba(255,255,255,.9);position:absolute;z-index:1000000;box-shadow:0 1px 5px -1px rgba(31,41,51,.2);-webkit-user-select:none;-moz-user-select:none;user-select:none;pointer-events:none}